*{
    margin: 0;
    padding: 0;
}
.flexbox-container{
    display: flex;
    align-content: flex-start;
    flex-wrap: wrap;
    justify-content: space-around;
}

.flexbox-item{
    border: 3px solid black;
    border-radius: 25px;
    padding: 10px;
    background-color: aliceblue;
}

.search-form{
    display: flex;
    justify-content: center;
    align-items: center;
    
}
.wrapper{
    background-color: lightcoral;
}

.search-input{
    width: 600px;
    height: 30px;
    border: none;
    padding: 15px;
    box-sizing: border-box;
}
.search-button{
    width: 60px;
    height: 30px;
    border: none;
    background-color: lightsalmon;
}